import fab from '../../components/fab/fab.vue';
let that = null;
export default {
	components:{
		fab
	},
	data() {
		return {
			isfirst: true,
			headerPosition: "fixed",
			headerTop: null,
			statusTop: null,
			showHeader: true,
			isLoginShow: false,
			Token: "",
			//个人信息,
			userinfo: {},
			// 订单类型
			orderList: [
				{
					text: '待发货',
					icon: "fahuo"
				},
				{
					text: '待收货',
					icon: "shouhuo"
				},
				{
					text: '待评价',
					icon: "pingjia"
				}
			],
			// 工具栏列表
			mytoolbarList: [{
					url: '../coupon/coupon',
					text: '优惠券',
					img: '/static/img/user/quan.png'
				}, 
				{
					url: '../address/address',
					text: '收货地址',
					img: '/static/img/user/addr.png'
				},
				{
					url: '../deposit/deposit',
					text: '账户数额',
					img: '/static/img/user/bank.png'
				}
			]
		}
	},
	onPullDownRefresh() {
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	onPageScroll(e) {
		this.headerPosition = e.scrollTop >= 0 ? "fixed" : "absolute";
		this.headerTop = e.scrollTop >= 0 ? null : 0;
		this.statusTop = e.scrollTop >= 0 ? null : -this.statusHeight + 'px';
	},
	onLoad() {
		that = this;
		this.statusHeight = 0;
		console.log(that.isLoginShow)
		if (that.isLoginShow) {
			that.initUserInfo()
		} else {
			uni.login({
				success(res) {
					if (res.code) {
						that.wxLoginStep(res.code)
					} else {
						that.$common.errorToShow('未取得code')
					}
				},
				fail() {
					that.$common.errorToShow('获取code失败')
				}
			})
		}
	},
	onShow() {

	},
	methods: {
		wxLoginStep(code) {
			this.$api.login({
				code
			}, (res) => {
				if (res.code) {
					that.Token = res.data.Token;
				} else {
					this.$common.errorToShow("获取openID异常", function() {
						uni.navigateBack({
							delta: 1
						})
					})
				}
			})
		},
		getUserInfo(res) {
			if (res.detail.errMsg == 'getUserInfo:fail auth deny') {
				that.$common.errorToShow('授权失败')
			} else {
				uni.getUserInfo({
					provider: 'weixin',
					success: function(infoRes) {
						infoRes.userInfo.Token = that.Token;
						that.toWxLogin(infoRes.userInfo)
					},
					fail(res) {

					}
				});
			}
		},
		toWxLogin(data) {
			let _this = this
			_this.$api.upinfoLogin(data, function(res) {
				if (res.code) {
					_this.isLoginShow = true;
					_this.userinfo = res.data[0];
					_this.$common.successToShow("登录成功")
					_this.$store.dispatch('setUserInfoAction', res.data[0])
					_this.$store.dispatch('saveUserTokenAction', _this.Token)
				} else {
					_this.$common.errorToShow('登录失败，请重试')
				}
			})
		},
		initUserInfo() {
			uni.getUserInfo({
				provider: 'weixin',
				success: function(infoRes) {
					that.$api.userInfoInit(infoRes.userInfo, res => {
						if (res.code) {
							that.userinfo = res.data[0];
							that.isLoginShow = true;
							that.$common.successToShow("更新信息成功")
							that.$store.dispatch('setUserInfoAction', that.userInfo)
						} else {
							that.$common.errorToShow('更新信息失败，请重试')
						}
					})
				},
				fail(res) {

				}
			});
		},
		//-----------------------
		toMsg() {
			uni.navigateTo({
				url: '../../msg/msg'
			})
		},
		toOrderList(index) {
			uni.setStorageSync('tbIndex', index);
			uni.navigateTo({
				url: '../order/order?tbIndex=' + index
			})
		},
		toLogin() {
			uni.showToast({
				title: '请登录',
				icon: "none"
			});
			uni.navigateTo({
				url: '../../login/login'
			})
			this.isfirst = false;
		},
		isLogin() {
			const value = uni.getStorageSync('UserInfo');
			if (value) {
				return true;
			}
			return false
		},
		toDeposit() {
			uni.navigateTo({
				url: '../../user/deposit/deposit'
			})
		},
		toPage(url) {
			if (!url) {
				uni.showToast({
					title: '模板未包含此页面',
					icon: "none"
				});
				return;
			}
			uni.navigateTo({
				url: url
			})
		},
		tomyOrder() {
			this.$common.navigateTo('/pages/order/order');
		}
	}
}
